import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-evenly',
    overflow: 'hidden',
    backgroundColor: 'rbga(0,0,0,0)',
    marginTop:10,
    marginBottom:10
    
  },
  gridList: {
    flexWrap: 'nowrap',
    // Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS.
    transform: 'translateZ(0)',
    height:200,
  },
  title: {
    fontSize:'20px',
    marginBottom:5,
    color: theme.palette.primary.light,
  },
  subtitle:{
    fontSize:'15px',
  },
  titleBar: {
    
    background:
      'linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)',
  },
  gridTileStyle:{
      height:100 
  }
}));

export default function Forcast(props) {

  const classes = useStyles();
  console.log(props.data)
  const data = props.data.splice(0,7)

  return (
    <div className={classes.root}>
      <GridList className={classes.gridList} cols={2.5}>
        {data.map((d) => (
             <GridListTile key={d.dt}>
             <img src='https://www.publicdomainpictures.net/pictures/320000/velka/background-image.png' alt='adawdwadawd' />
             <GridListTileBar
               title={Math.round(d.main.temp)}
               subtitle={(d.weather[0].main) + (new Date(d.dt_txt).getDay())}
               classes={{
                 root: classes.titleBar,
                 title: classes.title,
                 subtitle:classes.subtitle
               }}
             />
             <div>
                 Hi
             </div>
         </GridListTile>

        ))}



        
      </GridList>
    </div>
  );
}